<template>
  <div class="query-page">
    <query-column-list ref="columns" />
    <div class="query-part">
      <query-fields />
      <query-cnds />
      <query-orders />
    </div>
    <query-text :queryInfo="queryInfo" />
  </div>
</template>

<script>
import QueryColumnList from "./QueryColumnList.vue";
import QueryText from "./QueryText.vue";
import QueryFields from "./QueryFields.vue";
import QueryCnds from "./QueryCnds.vue";
import QueryOrders from "./QueryOrders.vue";
import { mapActions } from "vuex";

export default {
  name: "Query",
  components: { QueryColumnList, QueryText, QueryFields, QueryCnds, QueryOrders },
  provide() {
    return {
      query: this
    };
  },
  data() {
    return {
      currentIndex: 0,
      data: []
    };
  },
  props: {
    name: {
      type: String,
      required: true
    }
  },
  created() {
    this.fetchData();
  },
  watch: {
    $route: "fetchData"
  },
  computed: {
    currentData() {
      return this.data.length == 0 ? null : this.data[this.currentIndex];
    },
    selection: {
      get() {
        return this.currentData ? this.currentData.selection : [];
      },
      set(selection) {
        if (this.currentData == null) return;
        this.currentData.selection = selection;
      }
    },
    fields() {
      return this.currentData ? this.currentData.fields : [];
    },
    cnds() {
      return this.currentData ? this.currentData.cnds : [];
    },
    orders() {
      return this.currentData ? this.currentData.orders : [];
    },
    columns() {
      return this.currentData ? this.currentData.node.columns : [];
    },
    noSelection() {
      return this.selection.length == 0;
    },

    queryInfo() {
      let result = {
        entity: this.name,
        fields: []
      };
      let fields = result.fields;
      let cnds = [];
      let orders = [];
      let shrinks = [];
      let listProps = [];
      this.data.forEach(d => {
        if (d.node.link) {
          if (d.node.many) {
            if (d.fields.length > 0) {
              let listProp = { name: d.node.name, fields: [] };
              d.fields.forEach(f => listProp.fields.push(this.convertField(f)));
              if (d.cnds.length > 0) {
                listProp.conditions = [];
                d.cnds.forEach(c => listProp.conditions.push(this.convertCnd(c)));
              }
              if (d.orders.length > 0) {
                listProp.orders = [];
                d.orders.forEach(o => listProp.orders.push(this.convertOrder(o)));
              }
              if (d.pageSize) {
                listProp.pageSize = d.pageSize;
                listProp.pageNo = d.pageNo;
              }
              listProps.push(listProp);
            }
          } else {
            // 1v1 link
            let linkName = d.node.name;
            if (d.fields.length > 0) {
              d.fields.forEach(f => fields.push(this.convertField(f, linkName)));
              if (d.shrink) shrinks.push(linkName);
            }
            if (d.cnds.length > 0) d.cnds.forEach(c => cnds.push(this.convertCnd(c, linkName)));
            if (d.orders.length > 0) d.orders.forEach(o => orders.push(this.convertOrder(o, linkName)));
          }
        } else {
          if (d.fields.length > 0) d.fields.forEach(f => fields.push(this.convertField(f)));
          if (d.cnds.length > 0) d.cnds.forEach(c => cnds.push(this.convertCnd(c)));
          if (d.orders.length > 0) d.orders.forEach(o => orders.push(this.convertOrder(o)));
          if (d.pageSize) {
            result.pageSize = d.pageSize;
            result.pageNo = d.pageNo;
          }
        }
      });
      if (cnds.length > 0) result.conditions = cnds;
      if (orders.length > 0) result.orders = orders;
      if (shrinks.length > 0) result.shrinks = shrinks;
      if (listProps.length > 0) result.listProps = listProps;
      return result;
    }
  },
  methods: {
    ...mapActions(["getEntity"]),
    fetchData() {
      this.getEntity(this.name).then(node => {
        this.data = [
          {
            node,
            pageSize: 0,
            pageNo: 0,
            selection: [],
            fields: [],
            cnds: [],
            orders: []
          }
        ];
        node.links.forEach(link => {
          this.data.push({
            node: link,
            pageSize: 0,
            pageNo: 0,
            selection: [],
            fields: [],
            cnds: [],
            orders: []
          });
        });
        this.currentIndex = 0;
      });
    },
    clearSelection() {
      this.$refs.columnList.clearSelection();
    },
    actionClick(action) {
      this[action]();
    },
    goBack() {
      this.$router.go(-1);
    },
    convertField(f, link) {
      let result = link ? `${link}.${f.name}` : f.name;
      if (f.alias) result = result + ":" + f.alias;
      if (f.refinery) {
        result = result + "|" + f.refinery;
        if (f.param) result = result + "(" + f.param + ")";
      }
      return result;
    },
    convertCnd(c, link) {
      return {
        prop: link ? `${link}.${c.name}` : c.name,
        op: c.op,
        param: c.value == "" ? null : c.value
      };
    },
    convertOrder(o, link) {
      let result = link ? `${link}.${o.name}` : o.name;
      if (o.desc) result = result + " desc";
      return result;
    }
  }
};
</script>

<style lang="scss">
.query-page {
  display: flex;
  height: 100vh;

  .query-column-list {
    flex: 3;
  }
  .query-part {
    flex: 4;
    display: flex;
    flex-direction: column;
    margin: 0 2px;
  }
  .query-text {
    flex: 2;
  }
}
</style>
