<template>
  <div class="query-text">
    <block-head
      icon="el-icon-search"
      icon-action="query"
      title-action="changeFormat"
      title="查询代码"
      @action-click="actionClick"
    />
    <textarea :value="text" />
    <block-head
      icon="el-icon-close"
      icon-action="close"
      v-if="result"
      title="查询结果"
      @action-click="actionClick"
    />
    <textarea v-if="result" readonly :value="result" />
  </div>
</template>

<script>
import BlockHead from "./BlockHead.vue";
import { call } from "@/common/service.js";

export default {
  name: "QueryText",
  components: { BlockHead },
  data() {
    return {
      result: null,
      format: true,
      actions: [{ id: "copy", title: "复制", icon: "el-icon-document-copy" }]
    };
  },
  props: {
    queryInfo: Object
  },
  computed: {
    text() {
      return this.format
        ? JSON.stringify(this.queryInfo, null, 2)
        : JSON.stringify(this.queryInfo).replace(/"/g, "'");
    }
  },
  methods: {
    actionClick(action) {
      this[action]();
    },
    close() {
      this.result = null;
    },
    query() {
      call("data/query", {
        model: "EG",
        query: this.queryInfo
      }).then(data => (this.result = JSON.stringify(data, null, 2)));
    },
    changeFormat() {
      this.format = !this.format;
    }
  }
};
</script>

<style lang="scss">
.query-text {
  display: flex;
  flex-direction: column;
  & > textarea {
    width: 100%;
    flex: 1;
  }
}
</style>
