<template>
  <div class="sql-page">
    <block-head
      icon="el-icon-caret-left"
      icon-action="goBack"
      title="Sql查询"
      :actions="actions"
      @action-click="actionClick"
    />
    <textarea ref="input" />
    <block-head title="查询结果" />
    <textarea readonly :value="result" />
  </div>
</template>

<script>
import BlockHead from "./BlockHead.vue";
import { call } from "@/common/service.js";

export default {
  name: "QueryText",
  components: { BlockHead },
  data() {
    return {
      text: "",
      result: "",
      actions: [{ id: "query", title: "执行SQL", icon: "el-icon-search" }]
    };
  },
  props: {
    queryInfo: Object
  },
  methods: {
    actionClick(action) {
      this[action]();
    },
    goBack() {
      this.$router.go(-1);
    },
    query() {
      const input = this.$refs.input;
      let text = input.select();
      if (this._.isEmpty(text)) text = input.value;
      call("data/sql", { model: "EG", text }).then(
        data => (this.result = JSON.stringify(data, null, 2))
      );
    }
  }
};
</script>

<style lang="scss">
.sql-page {
  display: flex;
  flex-direction: column;
  height: 100vh;
  & > textarea {
    width: 100%;
    flex: 1;
  }
}
</style>
