<template>
  <div class="index-page">
    <item-tree-panel v-model="selectObject" />
    <div class="panel">
      <unit-panel v-show="isUnit" :unit="selectObject" @selectTable="entity => (selectObject = entity)" />
      <table-panel v-show="!isUnit" :entity="selectObject" />
    </div>
  </div>
</template>

<script>
import ItemTreePanel from "./ItemTreePanel.vue";
import UnitPanel from "./UnitPanel.vue";
import TablePanel from "./TablePanel.vue";

export default {
  name: "Index",
  components: { ItemTreePanel, UnitPanel, TablePanel },
  data() {
    return {
      selectObject: { name: "", label: "" }
    };
  },
  computed: {
    isUnit() {
      return this.selectObject.unit;
    }
  }
};
</script>

