<template>
  <div class="item-tree-panel line">
    <el-input class="filter" placeholder="输入关键字进行过滤" v-model="keywords" />
    <div class="tree-wrapper">
      <el-tree
        :data="tree"
        :filter-node-method="filterNode"
        :expand-on-click-node="false"
        node-key="id"
        ref="tree"
        :render-content="renderNode"
        highlight-current
        @current-change="nodeChange"
      >
      </el-tree>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "ItemTreePanel",
  data() {
    return {
      keywords: ""
    };
  },
  props: {
    value: Object
  },
  computed: {
    ...mapState(["tree"])
  },
  watch: {
    keywords(val) {
      this.$refs.tree.filter(val);
    },
    value(val) {
      if (val) this.$nextTick(() => this.$refs.tree.setCurrentKey(val.id));
    }
  },
  methods: {
    renderNode(h, { node, data }) {
      let icon = "el-icon-document";
      let label = data.label;
      if (data.root) {
        icon = "el-icon-s-home";
        node.expanded = true;
      } else if (data.unit) {
        icon = node.expanded ? "el-icon-folder-opened" : "el-icon-folder";
      }
      return (
        <span>
          <i class={icon} /> &nbsp;{label}
        </span>
      );
    },
    filterNode(value, data) {
      if (!value) return true;
      return data.label.indexOf(value) !== -1 || (data.name && data.name.indexOf(value) != -1);
    },
    nodeChange(data) {
      this.$emit("input", data);
    }
  }
};
</script>

<style lang="scss">
.item-tree-panel {
  display: flex;
  flex-direction: column;
  .tree-wrapper {
    border: 1px solid #eee;
    margin-top: 8px;
    flex: 1;
    overflow: scroll;
    .el-tree {
      cursor: pointer;
    }
  }
}
</style>
