<template>
  <div class="query-fields">
    <block-head
      title="查询属性"
      title-action="java"
      :actions="actions"
      @action-click="actionClick"
    />
    <div class="query-fields-wrapper">
      <el-table
        :data="query.fields"
        height="100%"
        :highlight-current-row="false"
        border
        row-key="id"
        cell-class-name="nopadding"
        header-cell-class-name="header-cell-class"
        ref="fieldsTable"
        @selection-change="s => (localSelection = s)"
      >
        <el-table-column fixed type="selection" :resizable="false" width="38" />
        <el-table-column prop="label" label="属性" min-width="160">
          <template slot-scope="scope">
            <span>{{ scope.row.label }}</span>
            <el-tag size="mini">{{ scope.row.name }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="alias" label="别名" min-width="100">
          <template slot-scope="scope">
            <el-input v-model="scope.row.alias" size="mini" />
          </template>
        </el-table-column>
        <el-table-column
          prop="refinery"
          label="加工方法"
          min-width="160"
          class-name="refinery"
        >
          <template slot-scope="scope">
            <template v-if="scope.row.refinery">
              <el-tag
                type="success"
                size="mini"
                @click="handleSet(scope.row)"
                >{{ scope.row.refinery }}</el-tag
              >
              <span v-if="scope.row.param">{{ scope.row.param }}</span>
            </template>
            <span v-else @click="handleSet(scope.row)">无</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <refinery-dialog ref="dialog" />
  </div>
</template>

<script>
import BlockHead from "./BlockHead.vue";
import { call } from "@/common/service.js";
import RefineryDialog from "./RefineryDialog";

export default {
  name: "QueryFields",
  components: { BlockHead, RefineryDialog },
  data() {
    return {
      localSelection: []
    };
  },
  computed: {
    actions() {
      return [
        {
          id: "addFields",
          title: "加入字段",
          icon: "el-icon-plus",
          disabled: this.query.selection.length == 0
        },
        {
          id: "delFields",
          title: "删除",
          icon: "el-icon-delete",
          disabled: this.localSelection.length == 0
        },
        {
          id: "autoRefinery",
          title: "自动处理",
          icon: "el-icon-thumb",
          disabled: this.query.fields.length == 0
        }
      ];
    }
  },
  inject: ["query"],
  methods: {
    actionClick(action) {
      this[action]();
    },
    addFields() {
      this.query.selection.forEach(s =>
        this.query.fields.push({ ...s, refinery: null, param: null })
      );
      this.query.$refs.columns.clearSelection();
    },
    delFields() {
      this.localSelection.forEach(field => {
        let inx = this.query.fields.indexOf(field);
        if (inx >= 0) this.query.fields.splice(inx, 1);
      });
    },
    handleSet(column) {
      if (!column.refineries) {
        const columnName =
          this.query.currentIndex == 0
            ? column.name
            : `${this.query.currentData.node.name}.${column.name}`;
        call("data/getRefinery", {
          model: "EG",
          entityName: this.query.name,
          columnName
        }).then(data => {
          column.refineries = data;
          if (data.length > 0) this.$refs.dialog.open(column);
        });
      } else if (column.refineries.length > 0) this.$refs.dialog.open(column);
    },
    autoRefinery() {
      this.query.fields.forEach(column => {
        if (column.refinery) return;
        if (!column.refineries) {
          const columnName =
            this.query.currentIndex == 0
              ? column.name
              : `${this.query.currentData.node.name}.${column.name}`;
          call("data/getRefinery", {
            model: "EG",
            entityName: this.query.name,
            columnName
          }).then(data => {
            column.refineries = data;
            if (data.length > 0) {
              const refinery = data[0];
              column.refinery = refinery.name;
              if (refinery.params.length > 0) column.param = refinery.params[0];
            }
          });
        } else if (column.refineries.length > 0) {
          const refinery = column.refineries[0];
          column.refinery = refinery.name;
          if (refinery.params.length > 0) column.param = refinery.params[0];
        }
      });
    },
    java() {
      let text = `public class ${this.query.name} {\n`;
      this.query.data[0].fields.forEach(f => {
        let type = "String";
        switch (f.type) {
          case ("SMALLINT", "INT"):
            type = "int";
            break;
          case "LONG":
            type = "long";
            break;
          case "DOUBLE":
            type = "double";
            break;
          case "NUMERIC":
            type = "BigDecimal";
            break;
          case "DATE":
            type = "LocalDate";
            break;
          case "TIME":
            type = "LocalTime";
            break;
          case "TIMESTAMP":
            type = "LocalDateTime";
            break;
          case "BLOB":
            type = "byte[]";
            break;
        }
        text += `\n\t//${f.label}\n`;
        text += `\tprivate ${type} ${f.name};\n`;
      });
      text += "}\n";
      const h = this.$createElement;
      this.$msgbox({
        title: "Java Class",
        message: h("textarea", { style: "width: 100%;height: 400px" }, [text]),
        confirmButtonText: "关闭"
      });
    }
  }
};
</script>

<style lang="scss">
.query-fields {
  flex: 3;
  display: flex;
  flex-direction: column;
  .query-fields-wrapper {
    flex: 1;
  }
  .refinery {
    user-select: none;
    cursor: pointer;
    &:hover {
      color: blue;
    }
  }
}
</style>
