import Vue from "vue";
import Router from "vue-router";
import Index from "./Index.vue";
import Query from "./Query.vue";
import SqlPage from "./SqlPage.vue";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "index",
      component: Index
    },
    {
      path: "/query/:name",
      name: "query",
      component: Query,
      props: true
    },
    {
      path: "sql",
      name: "sql",
      component: SqlPage
    }
  ]
});
