<template>
  <div class="query-entity" :class="{ current, main, used }" @click="$emit('click')">
    <el-button size="mini" type="text" @click="config">配置</el-button>
    <i class="el-icon-caret-right" />
    <span>{{ node.label }} </span>
    <el-tag size="mini">{{ infoText }}</el-tag>
  </div>
</template>

<script>
export default {
  name: "QueryEntity",
  props: {
    data: Object,
    current: Boolean,
    main: Boolean
  },
  computed: {
    node() {
      return this.data.node;
    },
    infoText() {
      let result = this.node.name;
      if (this.node.link) {
        if (this.node.many) {
          result = `${result}[ ]${this.pageText}`;
        } else if (this.data.shrink) {
          result = result + " (shrink)";
        }
      } else {
        result = result + this.pageText;
      }
      return result;
    },
    pageText() {
      if (this.data.pageNo && this.data.pageSize) {
        if (this.data.pageNo == 1) return ` 前${this.data.pageSize}条`;
        else return ` ${this.data.pageSize}条,第${this.data.pageNo}页`;
      } else return "";
    },
    used() {
      if (this.data.fields.length > 0) return true;
      if (this.data.cnds.length > 0) return true;
      if (this.data.orders.length > 0) return true;
      return false;
    }
  },
  methods: {
    config() {
      if (this.node.link && !this.node.many) {
        this.$set(this.data, "shrink", !this.data.shrink);
        return;
      }
      this.$parent.$refs.dialog.open(this.data);
    }
  }
};
</script>

<style lang="scss">
.query-entity {
  color: #aaa;
  cursor: pointer;
  & i {
    color: transparent;
  }
  &.used {
    color: black;
  }
  &.current {
    background-color: #eee;
    & i {
      color: black;
    }
  }
  &.main {
    border-bottom: 1px solid gray;
  }
}
</style>
