<template>
  <div class="query-column-list">
    <block-head
      icon="el-icon-caret-left"
      icon-action="goBack"
      :title="`查询生成器:${query.name}`"
      @action-click="actionClick"
    />
    <div class="head">
      <query-entity
        v-for="(data, index) in query.data"
        :key="index"
        :data="data"
        :current="index == query.currentIndex"
        :main="query.data.length > 1 && index == 0"
        @click="nodeChange(index)"
      />
    </div>
    <div class="body">
      <el-table
        :data="query.columns"
        height="100%"
        :highlight-current-row="false"
        border
        row-key="id"
        cell-class-name="nopadding"
        header-cell-class-name="header-cell-class"
        ref="columnList"
        @selection-change="s => (query.selection = s)"
      >
        <el-table-column fixed type="selection" :resizable="false" :selectable="row => !row.link" width="30" />
        <el-table-column prop="label" label="属性">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.link" type="success" size="mini">link</el-tag>
            <span>{{ scope.row.label }}</span>
            <el-tag size="mini">{{ scope.row.name }}</el-tag>
            <i v-if="scope.row.link && scope.row.link.many" class="el-icon-notebook-2" />
          </template>
        </el-table-column>
      </el-table>
    </div>
    <page-dialog ref="dialog" />
  </div>
</template>

<script>
import BlockHead from "./BlockHead.vue";
import QueryEntity from "./QueryEntity.vue";
import PageDialog from "./PageDialog.vue";

export default {
  name: "QueryColumnList",
  components: { BlockHead, QueryEntity, PageDialog },
  inject: ["query"],
  methods: {
    clearSelection() {
      this.$refs.columnList.clearSelection();
    },
    actionClick(action) {
      this[action]();
    },
    goBack() {
      this.$router.go(-1);
    },
    nodeChange(index) {
      this.query.currentIndex = index;
    }
  }
};
</script>

<style lang="scss">
.query-column-list {
  display: flex;
  flex-direction: column;
  .head {
    margin: 5px;
    padding: 5px;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    overflow-y: scroll;
    max-height: 30%;
  }
  .body {
    flex: 1;
    overflow: scroll;
  }
}
</style>
