<template>
  <el-dialog title="分页设置" width="280px" :visible.sync="visible">
    <el-form label-width="90px" size="mini" ref="form">
      <el-form-item label="不分页">
        <el-switch v-model="noPage" />
      </el-form-item>
      <el-form-item label="每页记录数">
        <el-input-number v-model="pageSize" :disabled="noPage" />
      </el-form-item>
      <el-form-item label="页数">
        <el-input-number v-model="pageNo" :disabled="noPage" />
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取 消</el-button>
      <el-button type="primary" @click="save">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: "RefineryDialog",
  data() {
    return {
      visible: false,
      entityData: null,
      noPage: false,
      pageSize: 30,
      pageNo: 1
    };
  },
  watch: {
    noPage: function(v) {
      if (!v) {
        if (!this.pageSize) this.pageSize = 30;
        if (!this.pageNo) this.pageNo = 1;
      }
    }
  },
  methods: {
    open(entityData) {
      this.entityData = entityData;
      if (entityData.pageSize) {
        this.noPage = false;
        this.pageSize = entityData.pageSize;
        this.pageNo = entityData.pageNo;
      } else {
        this.noPage = true;
        this.pageSize = 0;
        this.pageNo = 0;
      }
      this.visible = true;
    },
    save() {
      if (this.noPage) {
        this.entityData.pageNo = 0;
        this.entityData.pageSize = 0;
      } else {
        this.entityData.pageNo = this.pageNo;
        this.entityData.pageSize = this.pageSize;
      }
      this.visible = false;
    }
  }
};
</script>
