<template>
  <div class="query-orders">
    <block-head title="排序" :actions="actions" @action-click="actionClick" />
    <div class="body">
      <el-table
        :data="query.orders"
        height="100%"
        :highlight-current-row="false"
        border
        row-key="id"
        cell-class-name="nopadding"
        header-cell-class-name="header-cell-class"
        @selection-change="s => (localSelection = s)"
      >
        <el-table-column fixed type="selection" :resizable="false" width="38" />
        <el-table-column prop="label" label="属性" min-width="160">
          <template slot-scope="scope">
            <span>{{ scope.row.label }}</span>
            <el-tag size="mini">{{ scope.row.name }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="desc" label="倒序" min-width="100">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.desc" active-color="#13ce66" size="mini" />
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import BlockHead from "./BlockHead.vue";
export default {
  name: "QueryOrders",
  components: { BlockHead },
  data() {
    return {
      localSelection: []
    };
  },
  computed: {
    actions() {
      return [
        { id: "addFields", title: "加入字段", icon: "el-icon-plus", disabled: this.query.selection.length == 0 },
        { id: "delFields", title: "删除", icon: "el-icon-delete", disabled: this.localSelection.length == 0 }
      ];
    }
  },
  inject: ["query"],
  methods: {
    actionClick(action) {
      this[action]();
    },
    addFields() {
      const idSet = new Set(this.query.orders.map(f => f.id));
      this.query.selection.filter(s => !idSet.has(s.id)).forEach(s => this.query.orders.push({ ...s, desc: false }));
      this.query.$refs.columns.clearSelection();
    },
    delFields() {
      this.localSelection.forEach(field => {
        let inx = this.query.orders.indexOf(field);
        if (inx >= 0) this.query.orders.splice(inx, 1);
      });
    }
  }
};
</script>

<style lang="scss">
.query-orders {
  flex: 1;
  display: flex;
  flex-direction: column;
  .body {
    flex: 1;
    overflow: scroll;
  }
}
</style>
