<template>
  <div class="table-panel">
    <block-head :title="`${entity.label} - ${entity.name}`" :actions="tableActions" @action-click="actionClick" />
    <div class="table-panel-tag" v-if="entity.tags">
      <el-tag v-for="tag in entity.tags" size="mini" :key="tag">{{ tag }}</el-tag>
    </div>
    <div class="table-wrapper">
      <el-table
        :data="columns"
        height="100%"
        :highlight-current-row="false"
        border
        row-key="id"
        cell-class-name="nopadding"
        header-cell-class-name="header-cell-class"
        ref="fieldsTable"
      >
        <el-table-column fixed type="index" label="序号" width="50" />
        <el-table-column prop="label" label="显示标题" width="160" />
        <el-table-column prop="name" label="属性名" width="160" />
        <el-table-column prop="type" label="类型" width="140">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.link" type="success" size="mini">{{ scope.row.many ? "link列表" : "link" }}</el-tag>
            <span v-else>{{ scope.row.type }}</span>
          </template>
        </el-table-column>
        <el-table-column label="标签">
          <template slot-scope="scope" v-if="scope.row.tags">
            <el-tag v-for="tag of scope.row.tags" size="mini" :key="tag">{{ tag }}</el-tag>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import BlockHead from "./BlockHead.vue";
import { mapActions } from "vuex";

export default {
  name: "TablePanel",
  components: { BlockHead },
  data() {
    return {
      tableActions: [{ id: "query", title: "去查询", icon: "el-icon-search" }],
      columns: []
    };
  },
  props: {
    entity: Object
  },
  watch: {
    entity(newValue) {
      this.columns = [];
      if (!newValue || newValue.unit) return;
      this.getEntity(newValue.name).then(data => {
        this.columns = [...data.columns, ...data.links];
      });
    }
  },
  methods: {
    ...mapActions(["getEntity"]),
    actionClick(action) {
      this[action]();
    },
    query() {
      this.$router.push({ name: "query", params: { name: this.entity.name } });
    }
  }
};
</script>

<style lang="scss">
.table-panel {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  .table-panel-tag {
    display: flex;
  }
  .table-wrapper {
    flex: 1;
    overflow: hidden;
  }
}
</style>
