<template>
  <el-dialog title="设置数据处理方法" width="400px" :visible.sync="visible">
    <el-form
      :model="editObject"
      :rules="rules"
      label-width="90px"
      size="mini"
      ref="form"
    >
      <el-form-item label="属性名称" prop="label">
        <el-input v-model="column.label" readonly />
      </el-form-item>
      <el-form-item label="处理方法" prop="refinery">
        <el-select v-model="editObject.refinery">
          <el-option :value="null" label="无" />
          <el-option
            v-for="refinery in column.refineries"
            :value="refinery.name"
            :label="refinery.name"
            :key="refinery.name"
          />
        </el-select>
      </el-form-item>
      <el-form-item v-if="hasParam" label="处理参数" prop="refinery">
        <el-select
          v-model="editObject.param"
          :allow-create="canInput"
          :filterable="canInput"
        >
          <el-option v-for="item of paramList" :value="item" :key="item" />
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取 消</el-button>
      <el-button type="primary" @click="save">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: "RefineryDialog",
  data() {
    return {
      visible: false,
      refineries: [],
      editObject: { refinery: null, param: null },
      hasParam: false,
      canInput: false,
      paramList: [],
      column: {},
      rules: {
        code: [{ required: true, message: "请输入处理参数", trigger: "blur" }]
      }
    };
  },
  watch: {
    "editObject.refinery"(newValue) {
      this.setRefinery(newValue);
    }
  },
  methods: {
    open(column) {
      this.refineries = column.refineries;
      this.editObject.refinery = column.refinery;
      this.editObject.param = column.param;
      this.column = column;
      this.visible = true;
    },
    setRefinery(name) {
      const refinery =
        name == null ? null : this.refineries.find(r => r.name == name);
      if (refinery == null) {
        this.editObject.param = null;
        this.canInput = false;
        this.hasParam = false;
      } else {
        this.paramList = refinery.params;
        this.canInput = refinery.canInput;
        if (this.paramList == 0) {
          this.hasParam = this.canInput;
        } else {
          this.hasParam = true;
          if (this.editObject.param == null) {
            this.editObject.param = this.paramList[0];
          }
        }
      }
    },
    save() {
      this.$refs.form.validate(valid => {
        if (valid) this.doSave();
      });
    },
    doSave() {
      this.column.refinery = this.editObject.refinery;
      this.column.param = this.editObject.param;
      this.editObject.refinery = null;
      this.visible = false;
    }
  }
};
</script>
