<template>
  <div class="query-cnds">
    <block-head
      title="查询条件"
      :actions="actions"
      @action-click="actionClick"
    />
    <div class="body">
      <el-table
        :data="query.cnds"
        height="100%"
        :highlight-current-row="false"
        border
        row-key="id"
        cell-class-name="nopadding"
        header-cell-class-name="header-cell-class"
        @selection-change="s => (localSelection = s)"
      >
        <el-table-column fixed type="selection" :resizable="false" width="38" />
        <el-table-column prop="label" label="属性" min-width="100">
          <template slot-scope="scope">
            <span>{{ scope.row.label }}</span>
            <el-tag size="mini">{{ scope.row.name }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="op" label="Op" min-width="60">
          <template slot-scope="scope">
            <el-select v-model="scope.row.op" size="mini">
              <el-option
                v-for="item in opList"
                :key="item"
                :label="item"
                :value="item"
              >
              </el-option
            ></el-select>
          </template>
        </el-table-column>
        <el-table-column prop="value" label="参数" min-width="100">
          <template slot-scope="scope">
            <el-input v-model="scope.row.value" size="mini" />
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import BlockHead from "./BlockHead.vue";
export default {
  name: "QueryCnds",
  components: { BlockHead },
  data() {
    return {
      localSelection: [],
      opList: ["=", "<>", ">", ">=", "<", "<=", " like ", " not like ", " in "]
    };
  },
  computed: {
    actions() {
      return [
        {
          id: "addFields",
          title: "加入字段",
          icon: "el-icon-plus",
          disabled: this.query.selection.length == 0
        },
        {
          id: "delFields",
          title: "删除",
          icon: "el-icon-delete",
          disabled: this.localSelection.length == 0
        }
      ];
    }
  },
  inject: ["query"],
  methods: {
    actionClick(action) {
      this[action]();
    },
    addFields() {
      this.query.selection.forEach(s =>
        this.query.cnds.push({ ...s, op: "=", value: "" })
      );
      this.query.$refs.columns.clearSelection();
    },
    delFields() {
      this.localSelection.forEach(field => {
        let inx = this.query.cnds.indexOf(field);
        if (inx >= 0) this.query.cnds.splice(inx, 1);
      });
    }
  }
};
</script>

<style lang="scss">
.query-cnds {
  flex: 1;
  display: flex;
  flex-direction: column;
  .body {
    flex: 1;
    overflow: scroll;
  }
}
</style>
